.restaurantCard {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 1rem 2rem 1rem;
  margin-bottom: 5rem;
  width: 27rem;
  height: 22rem;
  border-radius: 0.8rem;
  box-shadow: -1px 3px 10px 0px rgba(0, 0, 0, 0.11);
  background-color: $white;

  &__img {
    width: 27rem;
    height: 13rem;
    object-fit: cover;
    border-radius: 0.7rem;
  }

  &__text {
    padding-top: 1.2rem;
    width: 100%;
    text-align: left;

    &--title {
      font-family: $titles;
      font-size: 1.4rem;
      font-weight: 800;
    }

    &--info {
      display: flex;
      gap: 1rem;
      padding: 0.5rem 0 2rem 0;

      &--p {
        font-family: $regular;
        font-size: 1.2rem;
      }
    }
  }

  &__buttons {
    width: 100%;
    display: flex;
    justify-content: space-between;

    &--reservation {
      font-family: $regular;
      padding: 0.7rem;
      background-color: $green;
      border: none;
      border-radius: 0.5rem;
      cursor: pointer;

      &:hover {
        background-color: $darkgreen;
        font-weight: 800;
      }

      &--i {
        padding-right: 1rem;
      }
    }

    &--menu {
      font-family: $regular;
      padding: 0.7rem;
      background-color: $orange;
      border: none;
      border-radius: 0.5rem;
      cursor: pointer;
      
      &:hover {
        background-color: $brightorange;
        font-weight: 800;
      }

      &--i {
        padding-right: 1rem;
      }
    }
  }
}
